import React, {Component} from 'react';
import bg2 from '../img/bg2.png';


class HomePage extends Component {
    render() {
        return (
            <div className="build-with-wrapper">
                <img src={bg2} alt=""/>
            </div>
        );
    }
}

export default  HomePage;


